@import url('https://fonts.googleapis.com/css2?family=Rajdhani:wght@300;400;500;600;700&display=swap');

@font-face {
    font-family: "Saol";
    font-weight: normal;
    font-style: normal;
    src: url("../fonts/saol.eot");
    src: url("../fonts/saol.eot?#iefix") format("embedded-opentype"),
      url("../fonts/saol.woff2") format("woff2"),
      url("../fonts/saol.woff") format("woff"),
      url("../fonts/saol.ttf") format("truetype");
    font-display: swap;
}

